export class Word {
    id: number;
    content: string;
    desc: string;
    level: number;
    user_id: number;

    constructor(data: any) {
        this.id = Number(data.id);
        this.content = data.content;
        this.desc = data.desc;
        this.level = Number(data.level);
        this.user_id = Number(data.user_id);
    }
}


